<template>
    <div ref="container7" class="container">
        <IntroduceChar :introduce="introduce">
            <div>
                <span>25km / h</span>
                <span>Top speed</span>
            </div>
            <div>
                <span>59 / nm</span>
                <span>Turbo boost torque</span>
            </div>
        </IntroduceChar>
    </div>
</template>
<script>
import IntroduceChar from "./char/introduceChar.vue"
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container} from "pixi.js"
export default {
    name:"webFirst",
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"Intelligent motor",
                content:"Tuned for maximum power and a natural ride-feel, our new motor runs near-silently – even at top speed.",
                left:0,
                top:0
            },
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container7).stage
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("Accessories.jpg","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web7/Accessories.jpg")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.35
            const brakeBike = new Sprite(this.loader.resources["Accessories.jpg"].texture)
            bicycleContainer.addChild(brakeBike)
            // brakeBike.x = this.container.clientWidth
            this.stage.addChild(bicycleContainer)
        },
    }
}
</script>
<style scoped>
.container ul li:nth-child(3) div{
    display: flex;
    flex-direction: column;
    margin: 20px 20px;
    margin-left:0px;
    margin-bottom:0px;
}
</style>