<template>
    <div ref="container4" class="container">
        <IntroduceChar :introduce="this.introduce">
            <div>
                <span>25km / h</span>
                <span>Top speed</span>
            </div>
            <div>
                <span>{{showNumber.toFixed(0)}} / nm</span>
                <span>Turbo boost torque</span>
            </div>
        </IntroduceChar>
    </div>
</template>
<script>
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container,Graphics} from "pixi.js"
import IntroduceChar from "./char/introduceChar.vue"
export default {
    name:"webFirst",
    data(){
        return{
            stage:null,
            loader:null,
            speedAdd:0.02,
            speed :0,
            introduce:{
                title:"Intelligent motor",
                content:"Tuned for maximum power and a natural ride-feel, our new motor runs near-silently – even at top speed.",
                left:60,
                top:0
            },

            counter:0,
            showNumber:0
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container4).stage
            this.loadResource()
            this.particleLoop = this.particleLoop.bind(this)
        })
    },
    watch:{
        counter(newValue){
            this.$gsap.to(this,{duration:1,showNumber:newValue})
        }
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("bike.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web4/bike.png")
            this.loader.add("motor.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web4/motor.png")
            this.loader.add("btn.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn.png")
            this.loader.add("btn_circle.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn_circle.png")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
            this.createButton()
            this.createParticle()
            this.bindEvent()
            this.particleStart()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.6
            const brakeBike = new Sprite(this.loader.resources["bike.png"].texture)
            const brakeMotor = new Sprite(this.loader.resources["motor.png"].texture)
            bicycleContainer.addChild(brakeBike)
            bicycleContainer.addChild(brakeMotor)
            brakeBike.alpha = 0.65
            bicycleContainer.x = -600
            bicycleContainer.y = -300
            // brakeBike.x = this.container.clientWidth
            this.stage.addChild(bicycleContainer)
        },
        createButton(){
            const btnContainer = new Container()
            const btn = new Sprite(this.loader.resources["btn.png"].texture)
            const btnCircle = new Sprite(this.loader.resources["btn_circle.png"].texture)
            const btnCircle2 = new Sprite(this.loader.resources["btn_circle.png"].texture)

            btnContainer.addChild(btn)
            btnContainer.addChild(btnCircle)
            btnContainer.addChild(btnCircle2)

            // 设置按钮和源泉的中心点 
            btn.pivot.x = btn.pivot.y = btn.width / 2
            btnCircle.pivot.x = btnCircle.pivot.y = btnCircle.width / 2
            btnCircle2.pivot.x = btnCircle2.pivot.y = btnCircle2.width / 2

            // 其中一个圆圈 要设置一个动效
            btn.scale.x = btn.scale.y = 0.4
            btnCircle.scale.x = btnCircle.scale.y = 0.4
            btnCircle2.scale.x = btnCircle2.scale.y = 0.5
            this.$gsap.to(btnCircle.scale,{duration:1,x:0.7,y:0.7,repeat:-1})
            this.$gsap.to(btnCircle,{duration:1,alpha:0,repeat:-1})

            // 设置按钮手指移动上去时鼠标变为可点击
            btnContainer.interactive = true;
            btnContainer.buttonMode = true;

            btnContainer.x = 450
            btnContainer.y = 320

            this.stage.addChild(btnContainer)
            this.btnContainer = btnContainer
        },
        createParticle(){
            this.particleContainer = new Container()
            this.particleContainer.pivot.x = window.innerWidth / 2
            this.particleContainer.pivot.y = window.innerHeight / 2
            this.particleContainer.x = window.innerWidth / 2
            this.particleContainer.y = window.innerHeight / 2

            this.particles = []
            this.particles2 = []
            const colors = [0xFFE720,0x000000]
            const len = 20
            this.createlizi(this.particles,colors[0],len,{x:300},{y:330})
            this.createlizi(this.particles2,colors[1],len,{x:200},{y:330})
            
            this.stage.addChild(this.particleContainer)
        },
        createlizi(particles,color,len,x,y){
            for(let i = 0; i < len; i++){
                const gr = new Graphics()
                const rotation = Math.floor(Math.random() * 360)
                // 填充颜色
                gr.beginFill(color)
                gr.drawCircle(0,0,3)
                gr.endFill()
                const pItem = {
                    rotation:rotation,
                    sx:Math.cos(rotation) * ( -100 ) + x.x ,
                    sy:Math.sin(rotation) * ( 200 ) + y.y ,
                    gr:gr
                }
                gr.x = pItem.sx
                gr.y = pItem.sy
                this.particleContainer.addChild(gr)
                particles.push(pItem)
            }
        },
        bindEvent(){
            this.btnContainer.on("mousedown",()=>{
                this.speedAdd = 2
                this.counter = 5000
            })
            this.btnContainer.on("mouseup",()=>{
                this.counter = 0
                this.speedAdd = 0.02
            })
        },
        particleStart(){
            this.$gsap.ticker.add(this.particleLoop)
        },
        particleLoop(){
            let speed = this.speed
            speed += this.speedAdd
            for (const pItem of this.particles) {
                pItem.gr.y = Math.sin(pItem.rotation + speed) * ( 200 ) + 300
                pItem.gr.x = Math.cos(pItem.rotation + speed) * ( -100 ) + 330
            }
            for (const pItem of this.particles2) {
                pItem.gr.y = Math.sin(pItem.rotation + speed) * ( 200 ) + 300
                pItem.gr.x = Math.cos(pItem.rotation + speed) * ( -100 ) + 230
            }
            this.speed = speed
        }
    }
}
</script>
<style scoped>
.container ul li:nth-child(3) div{
    display: flex;
    flex-direction: column;
    margin: 20px 20px;
    margin-left:0px;
    margin-bottom:0px;
}
</style>