<template>
    <div ref="videoContent" class="videoContent">
        <img src="../video/intro-gif-desktop.gif" alt="">
        <div ref="mess1" class="mess mess1"></div>
        <div ref="mess1" class="mess mess2"></div>
        <div ref="mess2" class="mess mess3"></div>
        <div ref="mess2" class="mess mess4"></div>
        <div class="title mess">Revolution, built in.</div>
    </div>
</template>
<script>
import {ScrollTrigger} from "gsap/dist/all.js"
export default {
    name:"homeView",
    data(){
        return{
            
        }
    },
    
    created(){
        
    },
    mounted(){
        this.$gsap.registerPlugin(ScrollTrigger)
        let mm = this.$gsap.matchMedia()
        mm.add({
            isDesktop:`(min-width:800px)`,
            isMobile:`(max-width:799px)`,
            reduceMotion:`(prefers-reduced-motion:reduce)`
        },(context)=>{
            console.log(context)
            let tl = this.$gsap.timeline({
                    scrollTrigger:{
                        // 选择固定住的class
                        trigger:".videoContent",
                        // 滚动条下滑与固定效果有一个缓冲的效果，并且持续2秒钟
                        scrub:2,
                        // 延长整个网页的长度，多出的长度是trigger选取的长度的200%
                        end:"100%",
                        // 引脚元素（如果已定义）。如果使用了选择器文本，例如“.pin”，则 pin 将是元素本身（不是选择器文本）
                        pin:true
                    }
                })
            tl.to(document.querySelectorAll(".mess"),{
                height:100+'%',
                width:100+'%',
                opacity:1
            })
        })
    },
    methods:{
        
    }
}
</script>
<style scoped>
.videoContent{
    width: 100%;
    height: 100vh;
    position: relative;
}
.videoContent img{
    position: relative;
    width:100%;
    height:100vh
}
.mess1,.mess2{
    position:absolute;
    top:0;
    /* width: 700px; */
    max-width: 500px;
    height: 100%;
    background-color: #fff;
}
.mess1{
    left:0;
}
.mess2{
    right:0;
}
.mess3,.mess4{
    position:absolute;
    left:0;
    width: 100%;
    /* height: 300px; */
    max-height: 250px;
    background-color: #fff;
}
.mess3{
    top:0;
}
.mess4{
    bottom:0;
}
.title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 45px;
    color: #fff;
    width: 100%;
    height: 100%;
    line-height: 100vh;
    opacity: 0;
}
</style>