<template>
    <div ref="container6" class="container showbike">
        <IntroduceChar :introduce="this.introduce"/>
    </div>
</template>
<script>
import IntroduceChar from "./char/introduceChar.vue"
import {ScrollTrigger} from "gsap/dist/all.js"
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container} from "pixi.js"
export default {
    name:"webSecond",
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"Super-smart electronics",
                content:"The Smart Cartridge controls all onboard systems, processing real-time motor feedback to ensure maximum range and motor responsiveness.",
                left:60,
                top:0
            },
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container6).stage
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("Cartridge.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web6/Cartridge.png")
            for(let i = 0;i<9;i++){
                this.loader.add(`bike${i}.png`,`https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web6/bike${i}.png`)
            }
            this.loader.add("btn.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn.png")
            this.loader.add("btn_circle.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn_circle.png")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
            this.createActionButton()
            this.bindEvent()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            const bicycleContainer2 = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.4
            bicycleContainer2.scale.x = bicycleContainer2.scale.y = 0.68
            
            const brakeBike = new Sprite(this.loader.resources["bike0.png"].texture)
            const brakeBike1 = new Sprite(this.loader.resources["bike1.png"].texture)
            const brakeBike2 = new Sprite(this.loader.resources["bike2.png"].texture)
            const brakeBike3 = new Sprite(this.loader.resources["bike3.png"].texture)
            const brakeBike4 = new Sprite(this.loader.resources["bike4.png"].texture)
            const brakeBike5 = new Sprite(this.loader.resources["bike5.png"].texture)
            const brakeBike6 = new Sprite(this.loader.resources["bike6.png"].texture)
            const brakeBike7 = new Sprite(this.loader.resources["bike7.png"].texture)
            const brakeBike8 = new Sprite(this.loader.resources["bike8.png"].texture)
            // const cartridge = new Sprite(this.loader.resources["Cartridge.png"].texture)
            brakeBike.y = -840
            brakeBike.x = -350
            bicycleContainer2.addChild(brakeBike)
            bicycleContainer.addChild(brakeBike7)
            bicycleContainer.addChild(brakeBike6)
            bicycleContainer.addChild(brakeBike2)
            bicycleContainer.addChild(brakeBike4)
            bicycleContainer.addChild(brakeBike3)
            bicycleContainer.addChild(brakeBike1)
            bicycleContainer.addChild(brakeBike5)
            bicycleContainer.addChild(brakeBike8)

            brakeBike1.x = 1140
            brakeBike1.y = 460

            brakeBike2.x = 10
            // brakeBike2.y = 210
            brakeBike2.y = 500

            brakeBike3.x = 5
            // brakeBike3.y = 200
            brakeBike3.y = 480

            brakeBike8.x = 1450
            // brakeBike8.y = 220
            brakeBike8.y = 500
            // 中间部分
            brakeBike4.y = 500

            // brakeBike5.y = 800
            brakeBike5.y = 500

            brakeBike6.x = 40
            // brakeBike6.y = 798
            brakeBike6.y = 500

            brakeBike7.y = 500
            // brakeBike7.x = -200
            brakeBike7.x = 70
            bicycleContainer.rotation = Math.PI / 180 * 15
            bicycleContainer.x = 150

            this.brakeBike1 = brakeBike1
            this.brakeBike2 = brakeBike2
            this.brakeBike3 = brakeBike3
            this.brakeBike4 = brakeBike4
            this.brakeBike5 = brakeBike5
            this.brakeBike6 = brakeBike6
            this.brakeBike7 = brakeBike7
            this.brakeBike8 = brakeBike8
            this.bicycleContainer2 = bicycleContainer2
            this.stage.addChild(bicycleContainer)
            this.stage.addChild(bicycleContainer2)
            
        },
        createActionButton() {
            const btnContainer = new Container();

            // 创建按钮和按钮外围的圈
            const btn = new Sprite(this.loader.resources['btn.png'].texture);
            const btnCircle = new Sprite(this.loader.resources['btn_circle.png'].texture);
            const btnCircle2 = new Sprite(this.loader.resources['btn_circle.png'].texture);

            btnContainer.addChild(btn);
            btnContainer.addChild(btnCircle);
            btnContainer.addChild(btnCircle2);

            // 设置按钮和圆圈的中心点
            btn.pivot.x = btn.pivot.y = btn.width / 2;
            btnCircle.pivot.x = btnCircle.pivot.y = btnCircle.width / 2;
            btnCircle2.pivot.x = btnCircle2.pivot.y = btnCircle2.width / 2;

            // 其中一个圆圈要设置一个动效
            btn.scale.x = btn.scale.y = 0.4
            btnCircle.scale.x = btnCircle.scale.y = 0.4
            btnCircle2.scale.x = btnCircle2.scale.y = 0.5
            this.$gsap.to(btnCircle.scale,{duration:1,x:0.7,y:0.7,repeat:-1})
            this.$gsap.to(btnCircle,{duration:1,alpha:0,repeat:-1})

            // 设置按钮手指移动上去时鼠标变为可点击
            btnContainer.interactive = true;
            btnContainer.buttonMode = true;
            btnContainer.x = 700
            btnContainer.y = 300
            this.stage.addChild(btnContainer);

            this.btnContainer = btnContainer;
        },
        bindEvent(){
            let that =this
            this.$gsap.registerPlugin(ScrollTrigger)
            let tl = this.$gsap.timeline()
            .to(this.bicycleContainer2,{
                    duration:2,
                    x:1100,
                    y:400,
                    onComplete:function(){
                        that.gsapAnimation()
                    }
                })
            ScrollTrigger.create({
                animation:tl,
                trigger:".showbike",
                onToggle: self => {
                    if(self.isActive){
                        let that = this
                        console.log(self.isActive)
                        this.$gsap.to(this.bicycleContainer2,{
                                duration:2,
                                x:1100,
                                y:400,
                                onComplete:function(){
                                    that.gsapAnimation()
                                }
                        })
                    }else{
                        console.log(self.isActive)
                        this.initgsapAnimation()
                    }
                },
            })

            this.btnContainer.on("mousedown", () => {
                this.$gsap.to(this.brakeBike1, { 
                        duration: 0.5, y : 460
                })
                this.$gsap.to([this.brakeBike2,this.brakeBike8,this.brakeBike5,this.brakeBike6], { 
                        duration: 0.5, y : 500
                })
                this.$gsap.to(this.brakeBike3, { 
                        duration: 0.5, y : 480
                })
                this.$gsap.to(this.brakeBike7, { 
                        duration: 0.5, x:70
                })
                this.$gsap.to(this.btnContainer,{
                    duration:0.5,alpha:0
                })
            });
            this.btnContainer.on("mouseup", () => {
                this.gsapAnimation()
            });
        },
        gsapAnimation(){
            this.$gsap.to(this.brakeBike1, { 
                    duration: 0.5, y:0
                });
                this.$gsap.to(this.brakeBike2, { 
                    duration: 0.5, y:210
                });
                this.$gsap.to(this.brakeBike3, { 
                    duration: 0.5, y:200
                });
                this.$gsap.to(this.brakeBike8, { 
                    duration: 0.5, y:220
                });
                this.$gsap.to(this.brakeBike5, { 
                    duration: 0.5, y:800
                });
                this.$gsap.to(this.brakeBike6, { 
                    duration: 0.5, y:798
                });
                this.$gsap.to(this.brakeBike7, { 
                    duration: 0.5, x:-200
                });
                this.$gsap.to(this.btnContainer,{
                    duration:0.5,alpha:1
                })
        },
        initgsapAnimation(){
           this.brakeBike1.x = 1140
           this.brakeBike1.y = 460
           this.brakeBike2.x = 10
           this.brakeBike2.y = 500
           this.brakeBike3.x = 5
           this.brakeBike3.y = 480
           this.brakeBike8.x = 1450
           this.brakeBike8.y = 500
           this.brakeBike4.y = 500
           this.brakeBike5.y = 500
           this.brakeBike6.x = 40
           this.brakeBike6.y = 500
           this.brakeBike7.y = 500
           this.brakeBike7.x = 70
           this.bicycleContainer2.x = 150
           this.bicycleContainer2.y = 0
        }
    }
}
</script>