<template>
    <div ref="container9" class="container">
       <IntroduceChar :introduce="introduce">
            <img src="https://www.vanmoof.com/sites/default/files/inline-files/EN_Apple_FindMy_Works_with_Badge_color_RGB_091620%201.png" alt="">
        </IntroduceChar>
    </div>
</template>
<script>
import IntroduceChar from "./char/introduceChar.vue"
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container} from "pixi.js"
export default {
    name:"webFirst",
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"Find your VanMoof",
                content:"The Apple Find My network provides an easy, secure way to locate your VanMoof on a map using the Find My app on your iPhone, iPad, iPod touch, or Mac.",
                left:0,
                top:0
            },
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container9).stage
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("ipad.jpg","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web9/ipad.jpg")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.4
            const brakeBike = new Sprite(this.loader.resources["ipad.jpg"].texture)
            brakeBike.x = -1200
            bicycleContainer.addChild(brakeBike)

            // brakeBike.x = this.container.clientWidth
            this.stage.addChild(bicycleContainer)
        },
    }
}
</script>
<style scoped>
img{
    width:50%;
    height:50%;
}
</style>