import { 
    Application,
    Loader,
    Container,
    // Sprite,
    Graphics,
    filters
} from "pixi.js";

export const application = function(container){
    let app = new Application({
        width:1000,
        height:625,
        backgroundColor:0xffffff,
        antialias:true,
        transparent:true
    })
    container.appendChild(app.view) 
    return app
}
export const newLoader = new Loader()
export const newContainer = new Container()
// export const newSprite = Sprite

export const newGraphics = new Graphics()
export const newFilters = new filters.BlurFilter()