<template>
  <div v-show="showHeader" class="header header2" ref="header">
   {{msg}}
  </div>
</template>

<script>
export default {
  name: 'HeaderSecond',
  props: {
    msg: String
  },
  data(){
    return{
        showHeader:false
    }
  },
  mounted(){
    window.onmousewheel = document.onmousewheel = this.scrollFunc
  },
  methods:{
    scrollFunc(e){
        e = e || window.event
        if(e.wheelDelta){ // 判断浏览器ie,谷歌滑轮事件
            if(e.wheelDelta > 0){ // 当滑轮向下滚动时
                console.log("滑轮向上滚动")
            }
            
            if(e.wheelDelta < 0){ // 当滑轮向上滚动时
                console.log("滑轮向下滚动")
            } 
            
        } else if (e.detail){ // Firefox滚轮事件
            if(e.detail > 0){ // 当滚轮向下滚动时
                console.log("滚轮向上滚动",e.detail)
            }
            if(e.detail < 0){ // 当滚轮向上滚动时
                console.log("滚轮向下滚动")
            }
        }
        const t = document.documentElement.scrollTop || document.body.scrollTop
        if(t == 0){
            this.showHeader = false
        }else{
            this.showHeader = true
        }
    },
  }
}
</script>

<style scoped>
.header{
  position: fixed;
  top: 64px;
  left: 0;
  background-color: skyblue;
  width: 100%;
  height: 64px;
  line-height: 64px;
  z-index: 999;
}
.header2{
  background-color: rgba(0, 0, 0, 0.2);
}
</style>
