<template>
    <div ref="container5" class="container">
        <IntroduceChar :introduce="this.introduce"/>
    </div>
</template>
<script>
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container,Graphics} from "pixi.js"
import IntroduceChar from "./char/introduceChar.vue"
export default {
    name:"webFirst",
    components:{
        IntroduceChar
    },
    data(){
        return{
            container:null,
            app:null,
            stage:null,
            loader:null,
            introduce:{
                title:"Hydraulic brake system",
                content:"Fully integrated hydraulic brakes deliver precision stopping power in all weather conditions",
                left:0,
                top:0
            },
        }
    },
    created(){
        this.$nextTick(()=>{
            this.container = this.$refs.container5
            this.stage = application(this.$refs.container5).stage
            this.particleLoop = this.particleLoop.bind(this)
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("brake_bike.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web5/brake_bike.png")
            this.loader.add("brake_handlerbar.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web5/brake_handlerbar.png")
            this.loader.add("brake_lever.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web5/brake_lever.png")
            this.loader.add("btn.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn.png")
            this.loader.add("btn_circle.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn_circle.png")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
            this.createActionButton()
            this.fixBibybleToRightBottom()
            this.bindEvent()
            this.createParticle()
            this.particleStart()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.3
            const brakeBike = new Sprite(this.loader.resources["brake_bike.png"].texture)
            const brakeHandlerBar = new Sprite(this.loader.resources["brake_handlerbar.png"].texture)
            const brakeLever = new Sprite(this.loader.resources["brake_lever.png"].texture)
            bicycleContainer.addChild(brakeBike)
            bicycleContainer.addChild(brakeHandlerBar)
            bicycleContainer.addChild(brakeLever)

            brakeLever.x = 722
            brakeLever.y = 900

            brakeBike.alpha = 0.8
            this.bicycleContainer = bicycleContainer
            this.bicycleContainer.x = 400
            this.brakeBike = brakeBike
            this.brakeLever = brakeLever
            this.brakeHandlerBar = brakeHandlerBar

            this.stage.addChild(bicycleContainer)
        },
        fixBibybleToRightBottom() {
            const resize = () => {
                // this.bicycleContainer.x = window.innerWidth - this.bicycleContainer.width;
                // this.bicycleContainer.y = window.innerHeight - this.bicycleContainer.height;

                this.btnContainer.x = this.bicycleContainer.x + this.bicycleContainer.width / 2;
                this.btnContainer.y = this.bicycleContainer.y + this.bicycleContainer.height / 2;
            }
            window.addEventListener('resize', resize);
            resize();
        },
        createActionButton() {
            const btnContainer = new Container();

            // 创建按钮和按钮外围的圈
            const btn = new Sprite(this.loader.resources['btn.png'].texture);
            const btnCircle = new Sprite(this.loader.resources['btn_circle.png'].texture);
            const btnCircle2 = new Sprite(this.loader.resources['btn_circle.png'].texture);

            btnContainer.addChild(btn);
            btnContainer.addChild(btnCircle);
            btnContainer.addChild(btnCircle2);

            // 设置按钮和圆圈的中心点
            btn.pivot.x = btn.pivot.y = btn.width / 2;
            btnCircle.pivot.x = btnCircle.pivot.y = btnCircle.width / 2;
            btnCircle2.pivot.x = btnCircle2.pivot.y = btnCircle2.width / 2;

            // 其中一个圆圈要设置一个动效
            btn.scale.x = btn.scale.y = 0.4
            btnCircle.scale.x = btnCircle.scale.y = 0.4
            btnCircle2.scale.x = btnCircle2.scale.y = 0.5
            this.$gsap.to(btnCircle.scale,{duration:1,x:0.7,y:0.7,repeat:-1})
            this.$gsap.to(btnCircle,{duration:1,alpha:0,repeat:-1})

            // 设置按钮手指移动上去时鼠标变为可点击
            btnContainer.interactive = true;
            btnContainer.buttonMode = true;

            this.stage.addChild(btnContainer);

            this.btnContainer = btnContainer;
        },
        bindEvent() {
            this.brakeLever.pivot.x = 455;
            this.brakeLever.pivot.y = 455;
            // 给按钮加上事件，按下的时候捏紧刹车，松开则慢慢回正
            this.btnContainer.on("mousedown", () => {
                this.$gsap.to(this.brakeLever, { 
                        duration: 0.5, rotation: Math.PI / 180 * -30 
                    })
                this.$gsap.to(this.brakeHandlerBar,{
                        duration: 0.5,y : 100
                    })
                this.$gsap.to(this.brakeBike,{
                        duration: 0.5,y : 100,alpha:1
                    })
                this.$gsap.to(this.brakeLever,{
                        duration: 0.5,y : 1000
                    })
                this.$gsap.to(this.btnContainer,{
                    duration:0.5,alpha:0
                })
                this.particlePause();
            });
            this.btnContainer.on("mouseup", () => {
                this.$gsap.to(this.brakeLever, { 
                    duration: 0.5, rotation: 0 
                    });
                this.$gsap.to(this.brakeHandlerBar,{
                        duration: 0.5,y : 0
                    })
                this.$gsap.to(this.brakeBike,{
                        duration: 0.5,y : 0,alpha:0.8
                    })
                 this.$gsap.to(this.brakeLever,{
                        duration: 0.5,y : 900
                    })
                this.$gsap.to(this.btnContainer,{
                    duration:0.5,alpha:1
                })
                this.particleStart();
            });
        },
        createParticle() {
            const particleContainer = new Container();

            particleContainer.rotation = Math.PI / 180 * 35;

            particleContainer.pivot.x = this.container.clientWidth / 2;
            particleContainer.pivot.y = this.container.clientHeight / 2;
            particleContainer.x = this.container.clientWidth / 2;
            particleContainer.y = this.container.clientHeight / 2;

            const particles = [];
            const colors = [0xf1cf54, 0xb5cea8, 0xf1cf54, 0x818181, 0x000000];
            const len = parseInt(Math.random() * 20) + 20;
            for (let i = 0; i < len; i++) {
                const gr = new Graphics();
                const idx = Math.floor(Math.random() * colors.length);
                gr.beginFill(colors[idx]);
                gr.drawCircle(0, 0, 6);
                gr.endFill();
                const pItem = {
                    // 初始坐标
                    sx: Math.random() * this.container.clientWidth,
                    sy: Math.random() * this.container.clientHeight,
                    // 粒子实例本身
                    gr: gr
                }
                gr.x = pItem.sx;
                gr.y = pItem.sy;
                particleContainer.addChild(gr);
                particles.push(pItem);
            }

            this.stage.addChild(particleContainer);

            this.particles = particles;
        },
        particleStart() {
            this.speed = 0;
            this.$gsap.ticker.add(this.particleLoop);
        },
        particlePause() {
            this.$gsap.ticker.remove(this.particleLoop);
            for (const pItem of this.particles) {
                this.$gsap.to(pItem.gr, { 
                    duration: 0.3, 
                    x: pItem.sx, 
                    y: pItem.sy,
                    ease: 'elastic.out' 
                });
                this.$gsap.to(pItem.gr.scale, { 
                    duration: 0.3, 
                    x: 1, 
                    y: 1,
                });
            }
        },
        particleLoop() {
            let speed = this.speed;
            speed += 0.2;
            speed = Math.min(speed, 20);
            for (const pItem of this.particles) {
                pItem.gr.y += speed;
                if (pItem.gr.y >= window.innerHeight) {
                    pItem.gr.y = 0;
                }
                pItem.gr.scale.x = Math.max(-0.1 * speed + 1, 0.3);
                pItem.gr.scale.y = Math.min(5 * speed + 1, 30);
            }
            this.speed = speed;
        }

    }
}
</script>