<template>
    <div ref="container1" class="container">
        <IntroduceChar :introduce="introduce" />
    </div>
</template>
<script>
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container} from "pixi.js"
import IntroduceChar from "./char/introduceChar.vue"
export default {
    name:"webFirst",
    components:{
        IntroduceChar
    },
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"Reframing the future",
                content:"With an updated motor, automatic electronic gear shifting, and integrated anti-theft tech, the VanMoof S3 & X3 are custom-tuned for the ultimate riding experience.",
                left:0,
                top:0
            }
        }
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container1).stage
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("bike1.jpg","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web1/bike1.jpg")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.4
            const brakeBike =new Sprite(this.loader.resources["bike1.jpg"].texture)
            bicycleContainer.addChild(brakeBike)
            this.stage.addChild(bicycleContainer)
        },
    
    }
}
</script>
