<template>
    <div ref="container3" class="container" 
    @mousedown="this.mousedownEvent"
    @mousemove="this.mousemoveEvent"
    @mouseup="this.mouseupEvent"
    >
        <IntroduceChar :introduce="this.introduce">
        </IntroduceChar>
    </div>
</template>
<script>
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container,Graphics,Text} from "pixi.js"
import IntroduceChar from "./char/introduceChar.vue"
export default {
    name:"webThird",
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"E-shifter",
                content:"Industry-first automatic electronic gear shifting gives the smoothest ride ever. With four speeds to flatten every urban incline.",
                left:0,
                top:0
            },
            speedAdd:0.02,
            speed :0,
            distance:50,

            // 数字递增
            counter:0,
            showNumber:0,
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container3).stage
            this.loadResource()
        })
    },
    watch:{
        counter(newValue){
            const text = newValue > 0 ? "KM/H" : ''
            this.$gsap.to(this.basicText,{duration:2,text:newValue + text})
        }
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("shifter.jpg","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web3/shifter.jpg")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
            this.createParticle()
            this.createButton()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            const brakeBike = new Sprite(this.loader.resources["shifter.jpg"].texture)
            brakeBike.scale.x = brakeBike.scale.y = 1.3
            brakeBike.x = 330
            bicycleContainer.addChild(brakeBike)
            this.bicycleContainer = bicycleContainer
            this.stage.addChild(bicycleContainer)
        },
        createButton(){
            const btnContainer = new Container()
            // 圆
            const circle = new Graphics();
            circle.beginFill(0xFF9A3D) 
            circle.lineStyle(4,0xFF9A3D,1) 
            circle.drawCircle(50,50,50)
            circle.endFill()
            circle.pivot.x = circle.pivot.y = circle.width / 2
            btnContainer.addChild(circle)
            // 文字
            const style = {
                fontFamily:"Arial",
                fontSize:"20px",
                fontStyle:"italic",
                fontWeight:"bold",
                wordWrap:true,
                wordWrapWidth:70
            }
            // "Press & Hold"
            const basicText = new Text("Press & Hold",style)
            basicText.x = -30 
            basicText.y = -20 
            btnContainer.addChild(basicText)
            btnContainer.x = 700
            btnContainer.y = 200

            btnContainer.interactive = true;
            btnContainer.buttonMode = true;

            this.btnContainer = btnContainer
            this.basicText = basicText
            this.stage.addChild(btnContainer)
        },
        mousedownEvent(e){
            this.counter = 25
            const left = this.$refs.container3.getBoundingClientRect().left
            const top = this.$refs.container3.getBoundingClientRect().top
            const x = e.clientX - left
            const y = e.clientY - top
            this.tl = this.$gsap.timeline()
            this.tl2 = this.$gsap.timeline()
            this.tl.to(this.btnContainer,{
                duration:0.3,
                x:x,
                y:y
            })
            this.tl2.to([
                this.particleContainer.scale,
                this.particleContainer2.scale,
                this.particleContainer3.scale,
                this.particleContainer4.scale
            ],{
                x:3,
                y:3,
            }).to([
                this.particleContainer2.scale,
                this.particleContainer3.scale,
                this.particleContainer4.scale
            ],{
                x:4,
                y:4,
            }).to([
                this.particleContainer3.scale,
                this.particleContainer4.scale
            ],{
                x:5,
                y:5,
            }).to(this.particleContainer4.scale,{
                x:6,
                y:6,
            })
            this.speedAdd = this.speedAdd * 100
            this.$gsap.ticker.add(this.particleLoop)
        },
        mousemoveEvent(e){
            const left = this.$refs.container3.getBoundingClientRect().left
            const top = this.$refs.container3.getBoundingClientRect().top
            const x = e.clientX - left
            const y = e.clientY - top
            this.tl.to(this.btnContainer,{
                duration:0.3,
                x:x,
                y:y
            })
        },
        mouseupEvent(){
            // 数字复原
            this.counter = "Press & Hold"
            this.tl.reverse()
            this.tl2.reverse()
            this.speedAdd = this.speedAdd / 100
            this.$gsap.ticker.remove(this.particleLoop)
        },
        createParticle(){
            this.particleContainer = new Container()
            this.particleContainer2 = new Container()
            this.particleContainer3 = new Container()
            this.particleContainer4 = new Container()
            this.particles = []
            this.particles2 = []
            this.particles3 = []
            this.particles4 = []
            const colors = [0xFFE720,0x000000]

            this.particleList(
                this.particleContainer,
                this.particles,
                colors[0]
            )
            this.particleList(
                this.particleContainer2,
                this.particles2,
                colors[1]
            )
            this.particleList(
                this.particleContainer3,
                this.particles3,
                colors[0]
            )
            this.particleList(
                this.particleContainer4,
                this.particles4,
                colors[1]
            )
            this.stage.addChild(this.particleContainer)
            this.stage.addChild(this.particleContainer2)
            this.stage.addChild(this.particleContainer3)
            this.stage.addChild(this.particleContainer4)
        },
        particleList(name,list,color){
            name.pivot.x = 660
            name.pivot.y = 315
            name.x = 660
            name.y = 315
            const len = 40
             
            for(let i = 0; i < len; i++){
                const gr = new Graphics()
                const rotation = Math.floor(Math.random() * 360)
                // 填充颜色
                gr.beginFill(color)
                gr.drawCircle(0,0,.5)
                gr.endFill()
                const pItem = {
                    rotation:rotation,
                    sx:Math.cos(rotation + this.speed) * ( this.distance ) + 660 ,
                    sy:Math.sin(rotation + this.speed) * ( this.distance ) + 315 ,
                    gr:gr
                }
                gr.x = pItem.sx
                gr.y = pItem.sy
                name.addChild(gr)
                
                list.push(pItem)
            }
        },
        particleLoop(){
            let speed = this.speed
            speed += this.speedAdd
            for (const pItem of this.particles) {
                pItem.gr.y = Math.sin(pItem.rotation + speed) * ( this.distance ) + 315
                pItem.gr.x = Math.cos(pItem.rotation + speed) * ( this.distance ) + 660
            }
            for (const pItem of this.particles2) {
                pItem.gr.y = Math.sin(pItem.rotation + speed) * ( this.distance ) + 315
                pItem.gr.x = Math.cos(pItem.rotation + speed) * ( this.distance ) + 660
            }
            for (const pItem of this.particles3) {
                pItem.gr.y = Math.sin(pItem.rotation + speed) * ( this.distance ) + 315
                pItem.gr.x = Math.cos(pItem.rotation + speed) * ( this.distance ) + 660
            }
            for (const pItem of this.particles4) {
                pItem.gr.y = Math.sin(pItem.rotation + speed) * ( this.distance ) + 315
                pItem.gr.x = Math.cos(pItem.rotation + speed) * ( this.distance ) + 660
            }
            this.speed = speed
        }
    }   
}
</script>
