<template>
    <div ref="container8" class="container">
        <IntroduceChar :introduce="introduce">
            <button>click know more</button>
        </IntroduceChar>
    </div>
</template>
<script>
import IntroduceChar from "./char/introduceChar.vue"
import {
    application
} from "../../plugin/pixi.js"
import {Sprite,Loader,Container} from "pixi.js"
export default {
    name:"webFirst",
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"The VanMoof App",
                content:"Unleash the full potential of the S3 & X3 with the VanMoof app. Unlock your bike effortlessly, configure everything from the gear change moments to the digital bell sound.",
                left:60,
                top:0
            },
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container8).stage
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("light.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web8/light.png")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })

        },
        show(){
            this.createBicycle()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.6
            const brakeBike = new Sprite(this.loader.resources["light.png"].texture)
            brakeBike.x = -400
            bicycleContainer.addChild(brakeBike)
            // brakeBike.x = this.container.clientWidth
            this.stage.addChild(bicycleContainer)
        },
    }
}
</script>
<style scoped>
button{
    background-color:#000;
    width:200px;
    height:50px;
    border-radius:25px;
    color:#fff;
    cursor: pointer;
}
</style>