<script>
import {
    application
} from "../../plugin/pixi.js"
import {ScrollTrigger} from "gsap/dist/all.js"
import {Sprite,Loader,Container,Graphics,filters} from "pixi.js"
import IntroduceChar from "./char/introduceChar.vue"
export default {
    name:"webSecond",
    data(){
        return{
            stage:null,
            loader:null,
            introduce:{
                title:"Theft Defense",
                content:"Effortless to use, useless to steal – onboard alarms and smart location tracking are specially designed to give bike thieves nightmares.",
                left:60,
                top:0
            },
        }
    },
    components:{
        IntroduceChar
    },
    created(){
        this.$nextTick(()=>{
            this.stage = application(this.$refs.container2).stage
            this.loadResource()
        })
    },
    methods:{
        loadResource(){
            this.loader = new Loader()
            this.loader.add("TheftDefense.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web2/TheftDefense.png")
            this.loader.add("map.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web2/map.png")
            this.loader.add("frame.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web2/frame.png")
            this.loader.add("banner.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web2/banner.png")
            this.loader.add("icons.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/web2/icons.png")
            this.loader.add("btn.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn.png")
            this.loader.add("btn_circle.png","https://lizhenglong-1304914507.cos.ap-nanjing.myqcloud.com/assets/button/btn_circle.png")
            this.loader.load()
            this.loader.onComplete.add(()=>{
                this.show()
            })
        },
        show(){
            this.createBicycle()
            this.createPhone()
            this.createButton()
            this.bindEvent()
            this.webinit()
        },
        createBicycle(){
            const bicycleContainer = new Container()
            bicycleContainer.scale.x = bicycleContainer.scale.y = 0.4
            const brakeBike = new Sprite(this.loader.resources["TheftDefense.png"].texture)
            bicycleContainer.addChild(brakeBike)
            brakeBike.x = -200
            this.stage.addChild(bicycleContainer)
        },
        createPhone(){
            // 制作一个圆角背景
            var graphics = new Graphics();
            graphics.beginFill(0x3e494b);
            graphics.lineStyle(4, 0x0, .3);
            graphics.drawRoundedRect(
                0,0,230,460,30
            );
            graphics.endFill();
            // 手机盒子
            const phoneContainer = new Container()
            const phoneFrame = new Sprite(this.loader.resources["frame.png"].texture)
            const phoneMap = new Sprite(this.loader.resources["map.png"].texture)
            phoneFrame.scale.x = phoneFrame.scale.y = 0.23
            phoneMap.scale.x = 0.58
            phoneMap.scale.y = 0.68
            phoneMap.x = -177
            phoneMap.y = -110
            // 覆盖圆角
            phoneMap.mask = graphics
            
            phoneContainer.addChild(phoneMap)
            phoneContainer.addChild(graphics)

            let mapcircle = new Graphics()
            let mapcircle2 = new Graphics()
            let mapcircle3 = new Graphics()
            let mapcircle4 = new Graphics()
            let mapcircle5 = new Graphics()
            let mapcircle6 = new Graphics()
            let mapcircle7 = new Graphics()
            let mapcircle8 = new Graphics()
            this.drawCircleModel(
                // 名字
                mapcircle,
                // 圆的大小
                50,
                // 颜色
                0xFF9A3D,
                // 透明度
                1,
                // x
                120,
                // y
                180
            )
            this.drawCircleModel(
                // 名字
                mapcircle2,
                // 圆的大小
                50,
                // 颜色
                0xFF9A3D,
                // 透明度
                0,
                // x轴
                120,
                // y轴
                180,
                // scale
                0
            )
            this.drawCircleModel(
                // 名字
                mapcircle3,
                // 圆的大小
                50,
                // 颜色
                0xFF9A3D,
                // 透明度
                0,
                // x轴
                120,
                // y轴
                180,
                // scale
                0
            )
            this.drawCircleModel(
                // 名字
                mapcircle4,
                // 圆的大小
                100,
                // 颜色
                0xffffff,
                // 透明度
                0,
                // x轴
                100,
                // y轴
                200,
                // scale
                1
            )
            this.drawCircleModel(
                // 名字
                mapcircle5,
                // 圆的大小
                30,
                // 颜色
                0xffffff,
                // 透明度
                0,
                // x轴
                20,
                // y轴
                200,
                // scale
                1
            )
            this.drawCircleModel(
                // 名字
                mapcircle6,
                // 圆的大小
                50,
                // 颜色
                0xffffff,
                // 透明度
                0,
                // x轴
                200,
                // y轴
                220,
                // scale
                1
            )
            this.drawCircleModel(
                // 名字
                mapcircle7,
                // 圆的大小
                60,
                // 颜色
                0xffffff,
                // 透明度
                0,
                // x轴
                80,
                // y轴
                350,
                // scale
                1
            )
            this.drawCircleModel(
                // 名字
                mapcircle8,
                // 圆的大小
                30,
                // 颜色
                0xffffff,
                // 透明度
                0,
                // x轴
                100,
                // y轴
                350,
                // scale
                1
            )
            phoneContainer.addChild(mapcircle8)
            phoneContainer.addChild(mapcircle7)
            phoneContainer.addChild(mapcircle6)
            phoneContainer.addChild(mapcircle5)
            phoneContainer.addChild(mapcircle4)
            phoneContainer.addChild(mapcircle)
            phoneContainer.addChild(mapcircle2)
            phoneContainer.addChild(mapcircle3)

            // 动画效果
            this.animation1 = this.$gsap.to(mapcircle.scale,{duration:3,x:1.1,y:1.1,repeat:-1})
            this.animation2 = this.$gsap.to(mapcircle2.scale,{duration:3,x:1,y:1,repeat:-1, ease: "power2.out"})
            this.animation3 = this.$gsap.to(mapcircle3.scale,{duration:3,x:1,y:1,repeat:-1,delay:1.5, ease: "power2.out"})
            // 先暂停
            this.animation2.pause()
            this.animation3.pause()

            // 制作一个模糊背景
            const blurFilter = new filters.BlurFilter()
            var graphics2 = new Graphics();
            graphics2.beginFill(0x3e494b);
            graphics2.lineStyle(4, 0x0, .3);
            graphics2.drawRoundedRect(
                0,410,230,60,30
            );
            phoneContainer.addChild(graphics2)
            graphics2.filters = [blurFilter]

            phoneContainer.addChild(phoneFrame)
            phoneContainer.x = 300
            phoneContainer.y = 100

            const phoneicons = new Sprite(this.loader.resources["icons.png"].texture)
            phoneContainer.addChild(phoneicons)
            phoneicons.scale.x = phoneicons.scale.y = 0.28
            phoneicons.y = 410
            phoneicons.x = 10

            const banner = new Sprite(this.loader.resources["banner.png"].texture)
            phoneContainer.addChild(banner)
            banner.scale.x = banner.scale.y = 0.28
            banner.x = 24
            // banner.y = 40
            banner.y = -40
            banner.alpha = 0

            this.phoneFrame = phoneFrame
            // this.graphics3 = graphics3
            this.mapcircle = mapcircle
            this.mapcircle2 = mapcircle2
            this.mapcircle3 = mapcircle3
            this.mapcircle4 = mapcircle4
            this.mapcircle5 = mapcircle5
            this.mapcircle6 = mapcircle6
            this.mapcircle7 = mapcircle7
            this.mapcircle8 = mapcircle8
            this.banner = banner
            
            this.stage.addChild(phoneContainer)
        },
        createButton(){
            const btnContainer = new Container()
            const btn = new Sprite(this.loader.resources["btn.png"].texture)
            const btnCircle = new Sprite(this.loader.resources["btn_circle.png"].texture)
            const btnCircle2 = new Sprite(this.loader.resources["btn_circle.png"].texture)

            btnContainer.addChild(btn)
            btnContainer.addChild(btnCircle)
            btnContainer.addChild(btnCircle2)

            // 设置按钮和源泉的中心点 
            btn.pivot.x = btn.pivot.y = btn.width / 2
            btnCircle.pivot.x = btnCircle.pivot.y = btnCircle.width / 2
            btnCircle2.pivot.x = btnCircle2.pivot.y = btnCircle2.width / 2

            // 其中一个圆圈 要设置一个动效
            btn.scale.x = btn.scale.y = 0.4
            btnCircle.scale.x = btnCircle.scale.y = 0.4
            btnCircle2.scale.x = btnCircle2.scale.y = 0.5
            this.$gsap.to(btnCircle.scale,{duration:1,x:0.7,y:0.7,repeat:-1})
            this.$gsap.to(btnCircle,{duration:1,alpha:0,repeat:-1})

            // 设置按钮手指移动上去时鼠标变为可点击
            btnContainer.interactive = true;
            btnContainer.buttonMode = true;

            btnContainer.x = 250
            btnContainer.y = 400
            this.btnContainer = btnContainer

            this.stage.addChild(btnContainer)
        },
        drawCircleModel(name,size,color,alpha = 0.5,x,y,scale = 1){
            name.beginFill(color) 
            name.lineStyle(4,color,1) 
            name.drawCircle(50,50,size)
            name.endFill()
            name.alpha = alpha
            name.pivot.x = name.pivot.y = name.width / 2
            name.x = x
            name.y = y
            name.scale.x = name.scale.y = scale
        },
        bindEvent(){
            // this.btnContainer.on("mousedown", () => {
            //     this.$gsap.to(this.btnContainer, { 
            //             duration: 0.5, alpha:0
            //     })
                
            // });
            this.btnContainer.on("mouseup", () => {
                this.$gsap.to(this.btnContainer, { 
                        duration: 0.5, 
                        alpha:0,
                        onComplete:()=>{
                            this.animation1.pause()
                            this.banneranimation = this.$gsap.to(this.banner,{
                                duration:1,
                                y:40,
                                alpha:1
                            })
                        }
                })
                this.tl = this.$gsap.timeline()
                this.tl.to(this.mapcircle,{
                    duration:0.5,
                    width:10,
                    height:10,
                })
                .to([this.mapcircle2,this.mapcircle3],{
                    duration:0.5,
                    alpha:0.5,
                    onComplete:()=>{
                        this.animation2.resume()
                        this.animation3.resume()
                    }
                })
                .to(this.mapcircle4,{
                    duration:0.3,
                    alpha:0.4,
                })
                .to(this.mapcircle5,{
                    duration:0.3,
                    alpha:0.4,
                })
                .to(this.mapcircle6,{
                    duration:0.3,
                    alpha:0.4,
                })
                .to(this.mapcircle7,{
                    duration:0.3,
                    alpha:0.4,
                })
                .to(this.mapcircle8,{
                    duration:0.3,
                    alpha:0.4,
                })
            });
        },
        webinit(){
            this.$gsap.registerPlugin(ScrollTrigger)
            ScrollTrigger.create({
                trigger:".showiphone",
                onToggle: self => {
                    if(self.isActive){
                        console.log(self.isActive)
                    }else{
                        this.btnContainer.alpha = 1
                        this.tl.revert()
                        this.banneranimation.revert()
                        this.animation1.resume()
                    }
                },
            })
        }
    }
}
</script>
<style scoped>
.container ul li:nth-child(3) div{
    display: flex;
    flex-direction: column;
    margin: 20px 20px;
    margin-left:0px;
    margin-bottom:0px;
}

</style>