<template>
    <div class="container" >
        <ul class="content" ref="content" v-if="this.choiceColor === 'block'">
            <li  
                class="content-item" 
                @mousedown="this.handleDragDown"
                @mousemove="this.handleDragMove"
                @mouseup="this.handleDragUp"
                v-for="(item,index) in this.newImgList" 
                :key="index">
                <img :src="require(`../assets/lunbotu1/lunbotu${index+1}.png`)" alt="">
            </li>
        </ul>
        <ul class="content" ref="content" v-else>
            <li v-show="this.choiceColor === 'blue'" 
                class="content-item" 
                @mousedown="this.handleDragDown"
                @mousemove="this.handleDragMove"
                @mouseup="this.handleDragUp"
                v-for="(item,index) in this.newImgList" 
                :key="index">
                <img :src="require(`../assets/lunbotu2/lunbotu${index+1}.png`)" alt="">
            </li>
        </ul>
        <div class="buttonContent" v-if="this.choiceColor === 'block'">
            <div v-show="this.choiceIndex1 !== 0"  class="button" @click="this.clickLeft ">
                <LeftButton/>
            </div>
            <div v-show="this.choiceIndex1 !== (this.newImgList.length - 1)" class="button" @click="this.clickRight">
                <RightButton/>
            </div>
        </div>
        <div class="buttonContent" v-else>
            <div v-show="this.choiceIndex2 !== 0"  class="button" @click="this.clickLeft ">
                <LeftButton/>
            </div>
            <div v-show="this.choiceIndex2 !== (this.newImgList.length - 1)" class="button" @click="this.clickRight">
                <RightButton/>
            </div>
        </div>
        <div class="choosebtn">
            <div class="color_choose">
                <div>
                    <div class="color" :class="this.choiceColor === 'block' ? 'active':''" @click="chooseColor('block')"></div>
                    <div class="color" :class="this.choiceColor === 'blue' ? 'active':''" @click="chooseColor('blue')"></div>
                </div>
                <div>
                    color:{{this.choiceColor}}
                </div>
            </div>
            <span  v-show="this.choiceColor == 'block'" 
                v-for="(item,index) in this.newImgList.length" 
                :key="index" class="choose_item" 
                @click="this.choose_number(index)">
                <span :class="this.choiceIndex1 === index ? 'active' : ''"></span>
            </span>
            <span  v-show="this.choiceColor == 'blue'" 
                v-for="(item,index) in this.newImgList.length" 
                :key="index" class="choose_item" 
                @click="this.choose_number(index)">
                <span :class="this.choiceIndex2 === index ? 'active' : ''"></span>
            </span>
            <div class="choose_img">
                <img src="../assets/tips/tips2.png" alt="">
            </div>
        </div>
    </div>
</template>
<script>
import LeftButton from "./button/LeftButton.vue"
import RightButton from "./button/RightButton.vue"

export default {
    components:{
        LeftButton,
        RightButton
    },
    name:"PicturesShow",
    data(){
        return{
            // 选择颜色
            choiceColor:"block",
            newImgList:[],
            choiceIndex1:0,
            choiceIndex2:0,
            imgList:[[
                {imgUrl:"../assets/lunbotu1/lunbotu1.png"},
                {imgUrl:"../assets/lunbotu1/lunbotu2.png"},
                {imgUrl:"../assets/lunbotu1/lunbotu3.png"},
                {imgUrl:"../assets/lunbotu1/lunbotu4.png"},
            ],[
                {imgUrl:"../assets/lunbotu2/lunbotu1.png"},
                {imgUrl:"../assets/lunbotu2/lunbotu2.png"},
                {imgUrl:"../assets/lunbotu2/lunbotu3.png"},
            ]],
            // 是否拖动
            ifHandleDrag:false,
            mouseDistantX:0,
            newMouseDistantX:0
        }
    },
    created(){
        // 获取相对的图片及其数量
        if(this.choiceColor == "block"){
            this.newImgList = this.imgList[0]
        }else if(this.choiceColor == "blue"){
            this.newImgList = this.imgList[1]
        }
    },
    mounted(){
        // 获取相对的content长度 400% / 300%
         this.$refs.content.style.width = `${this.newImgList.length*100}%`
    },
    methods:{
        // 向左一格
        clickLeft(){
            if(this.choiceColor === "block"){
                this.choiceIndex1--
                this.choose_number(this.choiceIndex1)
            }else if(this.choiceColor === "blue"){
                this.choiceIndex2--
                this.choose_number(this.choiceIndex2)
            }
        },
        // 向右一格
        clickRight(){
            if(this.choiceColor === "block"){
                this.choiceIndex1++
                this.choose_number(this.choiceIndex1)
            }else if(this.choiceColor === "blue"){
                this.choiceIndex2++
                this.choose_number(this.choiceIndex2)
            }
        },
        // 封装的到新的图片
        choose_number(number){
            if(this.choiceColor === "block"){
                this.choiceIndex1 = number
            }else if(this.choiceColor === "blue"){
                this.choiceIndex2 = number
            }
            this.$refs.content.style.left = `-${number*1000}px`
            const choose_item = document.querySelectorAll(".choose_item")
            for(let i = 0;i < choose_item.length;i++){
                choose_item[i].classList.remove("active")
            }
            choose_item[number].classList.add("active")
        },
        // 选择颜色
        chooseColor(color){
            this.choiceColor = color
            this.currentIndex = 0
            if(this.choiceColor === "block"){
                this.newImgList = this.imgList[0]
                // 获取当前图片index
                this.currentIndex = this.choiceIndex1
            }else if(this.choiceColor === "blue"){
                // 获取当前图片index
                this.currentIndex = this.choiceIndex2
                this.newImgList = this.imgList[1]
            }
            this.$nextTick(()=>{
                this.$refs.content.style.left = `-${this.currentIndex*1000}px`
                this.$refs.content.style.width = `${this.newImgList.length*100}%`
            })
        },
        // handleDown
        handleDragDown(e){
            this.ifHandleDrag = true
            this.mouseDistantX = e.clientX || e.touches[0].clientX
        },
        // handleMove
        handleDragMove(e){
            if(this.ifHandleDrag){
                this.newMouseDistantX = e.clientX || e.touches[0].clientX
            }else{
                return true
            }
        },
        // handleUp
        handleDragUp(){
            if(this.ifHandleDrag){
                if(this.newMouseDistantX - this.mouseDistantX > 500){
                    this.clickLeft()
                }else if(this.mouseDistantX - this.newMouseDistantX > 500) {
                    this.clickRight()
                }
                this.ifHandleDrag = false
                this.mouseDistantX = 0
                this.newMouseDistantX = 0
            }
            // console.log(e);
        }
    }
}
</script>
<style scoped>
.container{
    position: relative;
    /* margin: 100px; */
    width: 1000px;
    height: 625px;
    overflow: hidden;
    left: 50%;
    transform: translateX(-50%);
}
.content{
    position: absolute;
    left: 0;
    top: 0;
    width: 300%;
    transition: .5s;
}
.content-item {
    float: left;
    list-style: none;
}
.content-item img{
    width: 1000px;
    pointer-events: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-user-drag: none;
}
.buttonContent{
    position: absolute;
    width: 100%;
    height: 36px;
    /* background-color: pink; */
    top: 40%;
    padding: 0 100px;
    transform: translateY(-50%);
}
.button{
    width: 36px;
    height: 36px;
    background-color: black;
    color: #fff;
    border-radius: 50%;
    line-height: 36px;
    float: left;
}
.button:nth-child(2){
    float: right;
}
.choosebtn{
    position: absolute;
    width: 100%;
    height: 36px;
    /* background-color: pink; */
    top: 70%;
    transform: translateY(-50%);
    line-height: 36px;
}
.color_choose{
    position: absolute;
    left: 100px;
    width: 80px;
    height: 76px;
    bottom: 0;
}
.color_choose div{
    width: 100%;
    height: 32px;
    padding: 8px;
}
.color_choose div .color{
    position: relative;
    float: left;
    width: 32px;
    height: 32px;
    border-radius: 50%;
}
.color_choose div .color.active{
    background-color: #fff;
}
.color:nth-child(1){
    right: 5px;
}
.color::before{
    position: absolute;
    content:"";
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    right: 0;
    bottom: 0;
    background-color: #000;
    width: 16px;
    height: 16px;
    border-radius: 50%;
}
.color:nth-child(2)::before{
    background-color: #8ebed8;
}
.choose_img{
    position: absolute;
    right: 100px;
    width: 120px;
    height: 77px;
    bottom: 0;
}
.choose_img img{
    width: 100%;
    height: 100%;
}
.choose_item{
    position: relative;
    display: inline-block;
    width: 8px;
    height: 8px;
    margin: 0 3px;
    /* background-color: #989da2; */
    /* border-radius: 50%; */
}
.choose_item span.active{
    background-color: #000;
}
.choose_item span{
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #989da2;
}
</style>